import Vue from "vue";
import Router from "vue-router";
import NotFound from "./views/404.vue";
import Drama from "./views/Drama.vue";

Vue.use(Router);

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/404",
      name: "404",
      component: NotFound
    },
    {
      path: "",
      name: "default",
      redirect: "/drama"
    },
    {
      path: "/drama",
      name: "drama",
      component: Drama
    },
    {
      path: "/enter",
      name: "enter",
      component: () =>
        import(/* webpackChunkName: "enter" */ "./views/Enter.vue")
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.length === 0) {
    from.name
      ? next({
          name: from.name
        })
      : next("/404");
  } else {
    next(); // 如果匹配到正确跳转
  }
});

export default router;
